@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Resetting the document */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
	max-width: 800px;
	margin: auto;
	box-shadow: 5px 5px 10px 3px #333;
	position: relative;
}

header {
  background: linear-gradient(90deg, rgba(131,58,180,1) 0%,
                                     rgba(253,29,29,1) 50%, 
                                     rgba(252,176,69,1) 100%);
  padding: 30px 40px;
  color: white;
  text-align: center;
}

h1 {
  font-family: 'Acme', sans-serif;
  letter-spacing: 2px;
  margin-bottom: 15px;;
}

input {
  width: 75%;
  margin-right: 0;
  padding: 10px;
  border: none;
  font-size: 14px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.add-btn {
  width: 25%;
  padding: 10px;
  margin-left:0;
  background: #d9d9d9;
  color: #555;
  text-align: center;
  font-size: 14px;
  cursor: pointer;
  transition: 0.3s;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-weight: bold;
}

.add-btn:hover {
  background-color: #bbb;
  color: #FFF;
}

ul li {
  list-style-type: none;
  background: #eee;
  font-size: 14px;
  transition: 0.2s;
  font-family: Verdana, Geneva, Tahoma, sans-serif;


  /* make the list items unselectable */
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

ul li:nth-child(odd) {
  background: #cedce2;
}

ul li:hover {
  background: #ddd;
}

.todo-flex-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.todo-label {
  font-family: 'Acme', sans-serif;
} 

.delete-btn.MuiIconButton-root {
  margin-left: auto;
}

.pulsing-btn.MuiIconButton-root {
  margin-left: auto;
  background-color: rgba(255, 168, 168, 0.582);
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

.pulsing-btn.MuiIconButton-root:hover{
  background-color: rgba(255, 168, 168, 0.849);
}

@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
		box-shadow: 0 0 0 0 rgba(255, 171, 171, 0.7);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 10px rgba(255, 146, 146, 0);
	}

	100% {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
		box-shadow: 0 0 0 0 rgba(255, 169, 169, 0);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
		box-shadow: 0 0 0 0 rgba(255, 171, 171, 0.7);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 10px rgba(255, 146, 146, 0);
	}

	100% {
		-webkit-transform: scale(0.8);
		        transform: scale(0.8);
		box-shadow: 0 0 0 0 rgba(255, 169, 169, 0);
	}
}
